'use client'
import { useState } from "react";
import AsideShop from "./HeaderShop";

export default function Layout(){
    const [open,setOpen]=useState(false)
    return <aside className="bg-violet-500">
        <button onClick={()=>setOpen(!open)} className="text-5xl">+</button>
        {open&&<AsideShop/>}
    </aside>
}